'use client';

import { createContext, useContext, useState, ReactNode, useEffect } from 'react';
import { PostgresSaver } from '@langchain/langgraph-checkpoint-postgres';

interface Thread {
  id: string;
  title: string;
  lastMessage?: string;
  createdAt: Date;
  updatedAt: Date;
}

interface ThreadContextType {
  threads: Thread[];
  currentThreadId: string | null;
  setCurrentThreadId: (id: string | null) => void;
  addThread: (thread: Thread) => void;
  updateThread: (id: string, updates: Partial<Thread>) => void;
  deleteThread: (id: string) => void;
  checkpointer: PostgresSaver | null;
}

const ThreadContext = createContext<ThreadContextType | undefined>(undefined);

interface ThreadProviderProps {
  children: ReactNode;
  connectionString?: string;
}

export function ThreadProvider({ children, connectionString }: ThreadProviderProps) {
  const [threads, setThreads] = useState<Thread[]>([]);
  const [currentThreadId, setCurrentThreadId] = useState<string | null>(null);
  const [checkpointer, setCheckpointer] = useState<PostgresSaver | null>(null);

  useEffect(() => {
    if (connectionString) {
      const initializeCheckpointer = async () => {
        try {
          const checkpointer = PostgresSaver.fromConnString(connectionString);
          await checkpointer.setup();
          setCheckpointer(checkpointer);
        } catch (error) {
          console.error('Failed to initialize checkpointer:', error);
        }
      };
      initializeCheckpointer();
    }
  }, [connectionString]);

  const addThread = (thread: Thread) => {
    setThreads(prev => [...prev, thread]);
  };

  const updateThread = (id: string, updates: Partial<Thread>) => {
    setThreads(prev => prev.map(thread => 
      thread.id === id ? { ...thread, ...updates, updatedAt: new Date() } : thread
    ));
  };

  const deleteThread = (id: string) => {
    setThreads(prev => prev.filter(thread => thread.id !== id));
  };

  const value = {
    threads,
    currentThreadId,
    setCurrentThreadId,
    addThread,
    updateThread,
    deleteThread,
    checkpointer
  };

  return (
    <ThreadContext.Provider value={value}>
      {children}
    </ThreadContext.Provider>
  );
}

export function useThread() {
  const context = useContext(ThreadContext);
  if (!context) {
    throw new Error('useThread must be used within a ThreadProvider');
  }
  return context;
}
